.clearfix {
  @include clearfix();
}

// Hide content
.hide {
  display: none !important;
}

.invisible {
  visibility: hidden !important;
}

// Image replacement
.ir {
  @include hide-text();
}

// Separator
// Examples：
// <span class="sep">|</span>
// <span class="ndash">&ndash;</span>
.sep,
.ndash {
  margin: 0 0.25em;
  font-family: sans-serif;
}

// Symbol
.sym {
  font-family: sans-serif;
}
