.hi-preview{position:fixed;top:0;left:0;right:0;bottom:0;background:rgba(0,0,0,0.54);z-index:1800;-webkit-transition:background 0.3s ease-out;transition:background 0.3s ease-out}.hi-preview-leave{display:block !important}.hi-preview-leave.hi-preview-leave-active{background:rgba(0,0,0,0)}.hi-preview-leave.hi-preview-leave-active .hi-preview-image{width:0 !important;height:0 !important}.hi-preview-enter{background:rgba(0,0,0,0)}.hi-preview-enter .hi-preview-image{width:0 !important;height:0 !important}.hi-preview--hide{display:none}.hi-preview--width img{width:100%}.hi-preview--height img{height:100%}.hi-preview-image{position:absolute;display:inline-block;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);-webkit-transition:width 0.3s ease-out, height 0.3s ease-out;transition:width 0.3s ease-out, height 0.3s ease-out;overflow:hidden}
