.layout {
  min-width: 1260px; }
  .layout__header {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 11; }
  .layout__body {
    display: flex;
    box-sizing: border-box;
    min-height: 100vh;
    padding-top: 64px;
    transition: padding-left 0.15s linear;
    background-color: #fff; }
  .layout__content {
    box-sizing: border-box;
    height: 100%; }
  .layout__main {
    box-sizing: border-box;
    flex: 1;
    background-color: #fff; }
    .layout--collapsed .layout__main {
      margin-left: 48px; }
    .layout__main--lr {
      margin-left: 216px; }
  .layout__sidebar.sidebar {
    position: fixed;
    left: 0;
    top: 0;
    bottom: 0;
    z-index: 10; }
  .layout--card .layout__main {
    padding: 20px;
    background-color: #e7e7e7;
    width: calc(100% - 216px); }
  .layout--card .layout__content {
    background-color: #fff; }
